"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { MobileNavigation } from '@/components/indiko/navigation'
import Link from 'next/link'

interface Indicacao {
  id: string
  nomeAmigo: string
  dataIndicacao: string
  status: 'pendente' | 'confirmado' | 'recompensado'
  pontos?: number
}

export default function HistoricoPage() {
  const [indicacoes] = useState<Indicacao[]>([
    {
      id: '1',
      nomeAmigo: 'Maria Silva',
      dataIndicacao: '2024-01-15',
      status: 'recompensado',
      pontos: 100
    },
    {
      id: '2',
      nomeAmigo: 'João Santos',
      dataIndicacao: '2024-01-14',
      status: 'confirmado',
      pontos: 100
    },
    {
      id: '3',
      nomeAmigo: 'Ana Costa',
      dataIndicacao: '2024-01-13',
      status: 'pendente'
    },
    {
      id: '4',
      nomeAmigo: 'Carlos Oliveira',
      dataIndicacao: '2024-01-12',
      status: 'recompensado',
      pontos: 100
    },
    {
      id: '5',
      nomeAmigo: 'Fernanda Lima',
      dataIndicacao: '2024-01-11',
      status: 'pendente'
    }
  ])

  const [filtroAtivo, setFiltroAtivo] = useState<'todos' | 'pendente' | 'confirmado' | 'recompensado'>('todos')

  const indicacoesFiltradas = filtroAtivo === 'todos' 
    ? indicacoes 
    : indicacoes.filter(indicacao => indicacao.status === filtroAtivo)

  const getStatusBadge = (status: string) => {
    const statusConfig = {
      pendente: {
        variant: 'secondary' as const,
        color: 'bg-yellow-100 text-yellow-800',
        label: 'Pendente'
      },
      confirmado: {
        variant: 'secondary' as const,
        color: 'bg-blue-100 text-blue-800',
        label: 'Confirmado'
      },
      recompensado: {
        variant: 'secondary' as const,
        color: 'bg-green-100 text-green-800',
        label: 'Recompensado'
      }
    }

    const config = statusConfig[status as keyof typeof statusConfig]
    return (
      <Badge className={`${config.color} border-0`}>
        {config.label}
      </Badge>
    )
  }

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'pendente':
        return <div className="w-3 h-3 bg-yellow-500 rounded-full animate-pulse"></div>
      case 'confirmado':
        return <div className="w-3 h-3 bg-blue-500 rounded-full"></div>
      case 'recompensado':
        return <div className="w-3 h-3 bg-green-500 rounded-full"></div>
      default:
        return <div className="w-3 h-3 bg-gray-400 rounded-full"></div>
    }
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleDateString('pt-BR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    })
  }

  const totalPontos = indicacoes
    .filter(i => i.status === 'recompensado')
    .reduce((sum, i) => sum + (i.pontos || 0), 0)

  const estatisticas = {
    total: indicacoes.length,
    pendentes: indicacoes.filter(i => i.status === 'pendente').length,
    confirmadas: indicacoes.filter(i => i.status === 'confirmado').length,
    recompensadas: indicacoes.filter(i => i.status === 'recompensado').length
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-white pb-20">
      {/* Header */}
      <div className="bg-white shadow-sm">
        <div className="px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <Link href="/dashboard">
                <Button variant="ghost" size="sm" className="rounded-full">
                  ←
                </Button>
              </Link>
              <div>
                <h1 className="font-poppins font-bold text-xl text-gray-900">
                  Histórico
                </h1>
                <p className="text-sm text-gray-600">
                  Suas indicações e recompensas
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="px-6 py-6 space-y-6">
        {/* Estatísticas */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <Card className="bg-blue-50 border-blue-200">
            <CardContent className="p-4 text-center">
              <p className="text-2xl font-bold text-blue-600">{estatisticas.total}</p>
              <p className="text-sm text-blue-800">Total</p>
            </CardContent>
          </Card>
          
          <Card className="bg-yellow-50 border-yellow-200">
            <CardContent className="p-4 text-center">
              <p className="text-2xl font-bold text-yellow-600">{estatisticas.pendentes}</p>
              <p className="text-sm text-yellow-800">Pendentes</p>
            </CardContent>
          </Card>
          
          <Card className="bg-green-50 border-green-200">
            <CardContent className="p-4 text-center">
              <p className="text-2xl font-bold text-green-600">{estatisticas.recompensadas}</p>
              <p className="text-sm text-green-800">Confirmadas</p>
            </CardContent>
          </Card>
          
          <Card className="bg-purple-50 border-purple-200">
            <CardContent className="p-4 text-center">
              <p className="text-2xl font-bold text-purple-600">{totalPontos}</p>
              <p className="text-sm text-purple-800">Pontos</p>
            </CardContent>
          </Card>
        </div>

        {/* Filtros */}
        <Card className="shadow-md">
          <CardHeader>
            <CardTitle className="font-poppins font-bold text-lg">
              Filtrar por Status
            </CardTitle>
          </CardHeader>
          <CardContent>
            <Tabs value={filtroAtivo} onValueChange={(value) => setFiltroAtivo(value as any)}>
              <TabsList className="grid w-full grid-cols-4">
                <TabsTrigger value="todos" className="text-xs">Todos</TabsTrigger>
                <TabsTrigger value="pendente" className="text-xs">Pendente</TabsTrigger>
                <TabsTrigger value="confirmado" className="text-xs">Confirmado</TabsTrigger>
                <TabsTrigger value="recompensado" className="text-xs">Recompensado</TabsTrigger>
              </TabsList>
            </Tabs>
          </CardContent>
        </Card>

        {/* Lista de Indicações */}
        <Card className="shadow-md">
          <CardHeader>
            <CardTitle className="font-poppins font-bold text-lg">
              Suas Indicações ({indicacoesFiltradas.length})
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            {indicacoesFiltradas.length === 0 ? (
              <div className="text-center py-8">
                <div className="w-16 h-16 bg-gray-100 rounded-full mx-auto mb-4 flex items-center justify-center">
                  <div className="w-8 h-8 bg-gray-400 rounded-full"></div>
                </div>
                <p className="text-gray-500 mb-4">
                  Nenhuma indicação encontrada para este filtro
                </p>
                <Link href="/indicacao">
                  <Button className="bg-blue-600 hover:bg-blue-700">
                    Fazer Nova Indicação
                  </Button>
                </Link>
              </div>
            ) : (
              indicacoesFiltradas.map((indicacao) => (
                <div
                  key={indicacao.id}
                  className="flex items-center justify-between p-4 bg-white border border-gray-200 rounded-xl hover:shadow-md transition-shadow"
                >
                  <div className="flex items-center space-x-4">
                    <div className="flex items-center justify-center">
                      {getStatusIcon(indicacao.status)}
                    </div>
                    
                    <div className="flex-1">
                      <h3 className="font-semibold text-gray-900">
                        {indicacao.nomeAmigo}
                      </h3>
                      <p className="text-sm text-gray-600">
                        Indicado em {formatDate(indicacao.dataIndicacao)}
                      </p>
                    </div>
                  </div>

                  <div className="text-right space-y-2">
                    {getStatusBadge(indicacao.status)}
                    {indicacao.pontos && (
                      <p className="text-sm font-semibold text-green-600">
                        +{indicacao.pontos} pts
                      </p>
                    )}
                  </div>
                </div>
              ))
            )}
          </CardContent>
        </Card>

        {/* Ação Rápida */}
        {indicacoesFiltradas.length > 0 && (
          <div className="text-center">
            <Link href="/indicacao">
              <Button 
                size="lg"
                className="bg-green-600 hover:bg-green-700 text-white font-semibold rounded-xl shadow-lg hover:shadow-xl transition-all duration-300"
              >
                Indicar Mais Amigos
              </Button>
            </Link>
          </div>
        )}
      </div>

      <MobileNavigation />
    </div>
  )
}
